import { myDelivery,myKwDelivery,myMoney }  from '../../../api/index.js'
import KwDeliver from './kwDeliver.vue'
import RecyclableDeliver from './RecyclableDeliver.vue'
export default{
	components:{
		KwDeliver,
		RecyclableDeliver
	},
	data(){
		return{
			active:1,
			countProfit:0,
			count:0,
			page:1,
			userId:undefined,
			list:[],
			activeIds:[],
			onFetching:false,
			hasNextPage:true,
			tips:false,
		}
	},
	onLoad(){
		this.userId = uni.getStorageSync('userId')
		myMoney(this.userId).then(res=>{
			if(res.code==1){
				this.countProfit = res.data.countProfit
				this.count = res.data.count
			}
		})
		this.getData()
	},
	methods:{
		getData(){
			if(this.active==1){
				this.getDelivery()
			}else{
				this.getKwDelivery()
			}
		},
		changeActive(e){
			this.active = e.target.dataset.id
			this.page = 1
			this.list = []
			this.getData()
		},
		hanlderChange(id){
			var index = this.activeIds.indexOf(id)
			if(index==-1){
				this.activeIds.push(id)
			}else{
				this.activeIds.splice(index,1)
			}
		},
		getDelivery(){
			if(this.onFetching) return
			this.onFetching = true
			
			let data = {
				page:this.page,
				id:this.userId
			}
			myDelivery(data).then(res=>{
				this.onFetching = false
				if(res.code===1){
					this.list = this.list.concat(res.data.list)
					this.hasNextPage = res.data.hasNextPage
					this.page = res.data.hasNextPage ? ++this.page : this.page
				}
			})
		},
		getKwDelivery(){
			if(this.onFetching) return
			this.onFetching = true
			
			let data = {
				page:this.page,
				id:this.userId
			}
			myKwDelivery(data).then(res=>{
				this.onFetching = false
				if(res.code===1){
					this.list = this.list.concat(res.data.list)
					this.hasNextPage = res.data.hasNextPage
					this.page = res.data.hasNextPage ? ++this.page : this.page
				}
			})
		}
	},
	onReachBottom(){
		if(!this.hasNextPage){
			this.tips = true
			setTimeout(()=>{
				this.tips = false
			},2000)
			
			return
		}
		this.getData()
	}
	
}
